<template>
	<div>
		<h2>图片</h2>
    <el-row :gutter="20">
      <el-col :xs="12" :sm="8" class="type-selector">
        <el-select v-model="typeValue" placeholder="类型">
        	<el-option v-for="t in types" :value="t"></el-option>
        </el-select>
      </el-col>
