  		</el-col>
      <el-col :xs="12" :sm="8" class="site-selector">
        <el-select v-model="siteValue" placeholder="站子">
        	<el-option v-for="s in sites" :value="s"></el-option>
        </el-select>
      </el-col>
  	</el-row>
	</div>
</template>

<script>
export default {
  data () {
    return {
      sites: [
        'kaleidoscope_BN',
        'BONAPICS0819',
        'Rabbit_950819',
        'redballoon_95',
        'Flawless_L0819'
      ],
      siteValue: '',
      types: ['预览', '高清', '官方图'],
      typeValue: ''
    }
  }
}
</script>

<style>
  .el-col {
    padding-bottom: 10px;
  }
</style>



